@import 'global.scss';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  // background-color: $background-gray;
  // min-height: 800px;
  background-color: $base;
  min-width: 360px;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

h1, h2, h3, h4, h5 {
  color: $base;
}

.content-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;

  // border: solid 2px $base;
  // border-radius: 3px;
  // margin-top: 20px;
  // margin-bottom: 20px;
	background: linear-gradient(to bottom,
    $background-gray 0%, $background-gray 10%,
    $sub 60%, $sub 100%
  );
  text-align: center;
  background-repeat:space;
  
  .content {
    margin: 30px 0;

    display: flex;
    justify-content: center;
    align-items: center;

    text-align: center;
  }
}

.react-icons {
  // vertical-align: middle;
  margin-right: 5px;
}

label.required::after {
  content: "*";
  color: red;
}

::-webkit-scrollbar {
  height: 12px;
  width: 12px;
}

::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.6); 
  border-radius: 10px;
  // background: $base;
}

::-webkit-scrollbar-thumb {
  border-radius: 10px;
  -webkit-box-shadow: inset 0 0 4px $base;
  background: $background-gray;
}

// Toastify
.Toastify__toast-body {
  font-family: $font-codeforces;
  font-size: 14px;
}
.Toastify__toast--error {
  background: $red-lighter !important;
}
.Toastify__toast--success {
  background: $blue-lighter !important;
}
.Toastify__toast--warning {
  background: $orange-lighter !important;
}

@import "bootstrap/scss/functions";
@import "bootstrap/scss/variables";
@import "bootstrap/scss/mixins";
@import "bootstrap/scss/_buttons.scss";
.btn-dark {
  @include button-variant($base, darken($base, 7.5%), darken($base, 10%), lighten($base,5%), lighten($base, 10%), darken($base,30%));
  color: $sub;
  &:hover, &:focus {
    color: $sub;
  }
}
.btn-outline-dark {
  @include button-outline-variant($base, #222222, lighten($base,5%), $base);
  color: $sub;
}

@-webkit-keyframes rotating /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.rotating {
  -webkit-animation: rotating 2s linear infinite;
  -moz-animation: rotating 2s linear infinite;
  -ms-animation: rotating 2s linear infinite;
  -o-animation: rotating 2s linear infinite;
  animation: rotating 2s linear infinite;
}
.loading_3dot::after {
  display: inline-block;
  animation: dotty steps(1,end) 1s infinite;
  content: '';
}

@keyframes dotty {
    0%   { content: ''; }
    25%  { content: '.'; }
    50%  { content: '..'; }
    75%  { content: '...'; }
    100% { content: ''; }
}